//   background: linear-gradient($angle, $from 0%, $to 100%);
// }

@mixin show() {
  visibility: visible;
  opacity: 1;
  transition: opacity $transition-fast, visibility $transition-fast;
}

@mixin hide() {
  visibility: hidden;
  opacity: 0;
  transition: opacity $transition-fast, visibility $transition-fast;
}



